const extend = require('zhf.extend'); // 
const createElement = require('zhf.create-element'); // 
const Super = require('zhf.dom-components-super'); // ()

// 
class Sub extends Super {
    constructor(opts) {
        super(extend({
            // 
            callback: {},
            // 
            config: {
                icon: 'g-iconfont g-icon-no-data',
                text: '',
                button: {
                    isShowIcon: false,
                    icon: 'g-icon-home',
                    text: '',
                    href: '/',
                },
            },
        }, opts));
    }
}

// ()()()
Sub.prototype.moduleDomCreate = function () {
    const config = this.opts.config;
    const data = config;
    let buttonIconHtml = '';
    if (this.opts.config.button.isShowIcon) {
        buttonIconHtml = `<div class="g-button-icon ${data.button.icon}"></div>`;
    }
    this.moduleDom = createElement({
        style: config.moduleDomStyle,
        customAttribute: config.moduleDomCustomAttribute,
        attribute: {
            className: 'g-no-data',
            innerHTML: `
                <div class="g-no-data-icon g-iconfont ${data.icon}"></div>
                <div class="g-no-data-text">${data.text}</div>
                <a class="g-no-data-button g-button" href="${data.button.href}">
                    ${buttonIconHtml}
                    <div class="g-button-text">${data.button.text}</div>
                </a>
            `,
        },
    });
};

// ()()()
Sub.prototype.power = function () {
};

module.exports = Sub;



