const extend = require('zhf.extend'); // 
const timeCountDown = require('zhf.time-count-down'); // 
const Confirm = require('../g-confirm');

// 
class Super {
    constructor(opts) {
        opts = extend({
            title: '',
            seconds: 3,
            pageTitle: '',
            href: 'javascript:;',
        }, opts);
        const content = `<div class="g-jump-link">
            <div class="g-jump-link-title">${opts.title}</div>
            <div class="g-jump-link-body">
                <div class="g-jump-link-body-seconds">${opts.seconds}</div>
                <div></div>
                <a href="${opts.href}" class="g-jump-link-body-page-title">${opts.pageTitle}</a>
            </div>
            <a href="${opts.href}" class="g-jump-link-footer"></a>
        </div>`;
        const oConfirm = new Confirm({
            config: {
                positionLocation: 'center', // 
                isShowHeader: false, // 
                headerContent: ':', // 
                isShowBody: true, // 
                content: content, // 
                isShowFooter: false, // 
                isShowClose: false, // 
                closeContent: '<div class="g-iconfont g-icon-close"></div>', // 
                isShowConfirm: false, // 
                confirmContent: '', // 
                isShowCancel: false, // 
                cancelContent: '', // 
                isCustom: false, // 
                isShowIcon: false, // icon
                icon: 'g-icon-warning', // icon
                isShowMask: true, // 
                isHandHide: false, // ()
            },
        });
        const seconds = oConfirm.moduleDom.querySelector('.g-jump-link-body-seconds');
        timeCountDown({
            seconds: opts.seconds,
            callback: {
                run: function (obj) {
                    seconds.innerHTML = obj.seconds;
                },
                over: function () {
                    window.location.href = opts.href;
                },
            },
        });
    }
}

module.exports = Super;



