import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

// 
const state = {
    count: 10,
};

const mutations = {
    add(context) {
        context.count++;
    },
    decrease(context) {
        context.count--;
    },
};

// 
// 
const actions = {
    add(add) {
        add.commit('add');
    },
    decrease(decrease) {
        decrease.commit('decrease');
    },
    oddAdd({commit, state}) {
        if (state.count % 2 === 0) {
            commit('add');
        }
    },
};

// 
const getters = {
    count(context) {
        return context.count;
    },
    getOdd(context) {
        return context.count % 2 === 0 ? '' : '';
    },
};

/*
* state: 
* getters:  store  state  store 
* mutations:  Vuex  store  mutation
* Action:  mutation
*    mutation
*   
* */
export default new Vuex.Store({
    state: {
        isShowCopyright: true,
        footerNav: {
            config: {
                isShow: true,
            },
            data: [
                {
                    href: '/',
                    text: '',
                    icon: 'icon-home',
                    isHighlight: true,
                    isShowMark: false,
                },
                {
                    href: '/mine/',
                    text: '',
                    icon: 'icon-mine',
                    isHighlight: false,
                    isShowMark: false,
                },
            ],
        },
    },
    getters: {},
    mutations: {},
    actions: {},
});



